<template>
  <div class="teacher-class-list">
    <div class="teacher-class-header">
      <span class="title">我的班级</span>
      <span class="iconfont icon-pailie1" :class="{'choosed':isList}" @click="isList = true"></span>
      <span class="iconfont icon-pailie" :class="{'choosed':!isList}" @click="isList = false"></span>
    </div>
    <div class="class-box">
      <!-- 卡片样式 -->
      <div
        v-show="!isList"
        class="class-item"
        v-for="(item,index) in classItems"
        :key="index"
        :class="{'no-m-r':(index+1)%3 == 0}">
        <div class="panel">
          <i class="delete-btn iconfont icon-shanchu" @click="deleteHandler(item)"></i>
          <div class="main-box" @click="toDetail(item.link)">
            <div class="title">{{item.title}}</div>
            <div class="students">
              <div class="number">男生：{{item.boys}}</div>
              <div class="line"></div>
              <div class="number">女生：{{item.girls}}</div>
            </div>
          </div>
        </div>
        <div class="count-num" @click="toDetail(item.link)">
          <div>
            <span class="iconfont icon-fangkuai grown-text"></span>
            <span class="text">迟到：{{item.later}}</span>
          </div>
          <div>
            <span class="iconfont icon-fangkuai green-text"></span>
            <span class="text">早退：{{item.earlyer}}</span>
          </div>
          <div>
            <span class="iconfont icon-fangkuai red-text"></span>
            <span class="text">旷课：{{item.misser}}</span>
          </div>
        </div>
      </div>
      <!-- 列表样式 -->
      <div class="class-list" v-show="isList">
        <el-table :data="classItems" style="width: 100%">
          <el-table-column label="班级名称" width="180">
            <template slot-scope="scope">
              <router-link :to="scope.row.link">{{scope.row.title}}</router-link>
            </template>
          </el-table-column>
          <el-table-column prop="total" label="总人数"></el-table-column>
          <el-table-column prop="boys" label="男生人数"></el-table-column>
          <el-table-column prop="girls" label="女生人数"></el-table-column>
          <el-table-column prop="later" label="迟到"></el-table-column>
          <el-table-column prop="earlyer" label="早退"></el-table-column>
          <el-table-column prop="misser" label="旷课"></el-table-column>
          <el-table-column label="操作">
            <template>
              <i class="iconfont icon-shanchu"></i>
            </template>
          </el-table-column>
        </el-table>
        <div class="course-page" style="margin-top:36px">
          <el-pagination layout="prev, pager, next" :page-size="10" :total="allItems"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      allItems: 20,
      classItems: [
        {
          title: "三年(2)班",
          link: "/main/teacherSpace/classMana/view/123",
          total: 52,
          boys: 22,
          girls: 30,
          later: 7,
          earlyer: 0,
          misser: 0
        },
        {
          title: "三年(2)班",
          link: "/main/teacherSpace/classMana/view/456",
          total: 52,
          boys: 22,
          girls: 30,
          later: 7,
          earlyer: 0,
          misser: 0
        },
        {
          title: "三年(2)班",
          link: "/main/teacherSpace/classMana/view/789",
          total: 52,
          boys: 22,
          girls: 30,
          later: 7,
          earlyer: 0,
          misser: 0
        },
        {
          title: "三年(2)班",
          link: "/main/teacherSpace/classMana/view/101",
          total: 52,
          boys: 22,
          girls: 30,
          later: 7,
          earlyer: 0,
          misser: 0
        }
      ],
      isList: false
    };
  },
  methods: {
    toDetail(path) {
      this.$router.push(path);
    },
    deleteHandler(item) {}
  }
};
</script>
<style lang="less" scoped>
.teacher-class-list {
  width: 100%;
  .class-box {
    width: 100%;
    margin-top: 18px;
    .no-m-r {
      margin-right: 0 !important;
    }
    .class-item {
      width: 286px;
      height: 180px;
      background: #fff;
      margin-right: 33px;
      margin-bottom: 22px;
      float: left;
      .panel {
        width: 100%;
        height: 80%;
        background: #3fabff;
        .delete-btn {
          color: #fff;
          float: right;
          font-size: 1.1rem;
          margin-top: 12px;
          margin-right: 12px;
        }
        .main-box {
          width: 100%;
          height: 112px;
          padding-top: 22px;
          .title {
            font-size: 1.8rem;
            margin: 25px auto;
            margin-top: 12px;
            color: #fff;
          }
          .students {
            width: 100%;
            color: #fff;
            div {
              float: left;
            }
            .number {
              width: 49%;
              margin-top: 5px;
            }
            .line {
              width: 1px;
              height: 32px;
              background: #fff;
            }
          }
        }
      }
      .count-num {
        width: 100%;
        height: 20%;
        div {
          width: 33.33%;
          height: 100%;
          float: left;
          line-height: 36px;
          .text {
            font-size: 0.9rem;
          }
          .iconfont {
            font-size: 0.8rem !important;
          }
        }
      }
      &:hover {
        cursor: pointer;
      }
    }
    .class-list {
      width: 100%;
      padding: 18px;
      background: #fff;
      a {
        text-decoration: none !important;
        color: #3fabff;
      }
      .iconfont {
        padding: 3px;
        background: #3fabff;
        color: #fff;
        border-radius: 6px;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
}
</style>



